html{
  font-size: 62.5%; /* 16px defualt size x 62.5 = 10 px = 1rem*/
  box-sizing: border-box;
}
body{
  margin:0;
  height: 100vh;
  font-size: 1.6rem; 
  font-family: Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root{
  height: 100%;
}

/* Layout */
.grid-container{
  display: grid;
  grid-template-areas:
  'header'
  'main'
  'footer';
  grid-template-columns: 1fr; /*1 colum*/
  grid-template-rows: 5rem 1fr 5rem; /*3 rows*/
  height: 100%;
}

header{
  grid-area: header;
  background-color: #203040;
}
main{
  grid-area: main;
  padding: 1rem;
}
footer{
  grid-area:footer;
  background-color: #203040;
  color:#ffffff;
}


/* Common */
h1{
  font-size: 1.8rem;
  padding: 1rem 0;
}
h2{
  font-size: 1.6rem;
  padding: 1rem 0;
}
a{
  text-decoration: none;
}

/*
a:link - a normal, unvisited link
a:visited - a link the user has visited
a:hover - a link when the user mouses over it
a:active - a link the moment it is clicked
*/
a:hover{
  color: #ff8000;
}

/* meaning all linkes in the header */
header a{
  color:#ffffff;
  padding: 1rem;
}

a.brand{
  color: #ffffff;
  font-size: 3rem;
  font-weight: bold;
}
.row{
  display: flex;
  flex-wrap: wrap;
  justify-content:space-between;
  align-items:center;
}

.row.center{
  justify-content: center;
}


/* Image */
img{
  border-radius: 0.5rem;
}

img.medium{
  max-width:  29rem;
  width: 100%;
}

.card{
  border: 0.1rem #c0c0c0 solid;
  background-color: #f8f8f8;
  border-radius: 0.5rem;
  margin: 1rem;
}

.card-body{
  padding: 1rem;
}

.card-body > *{
  margin-bottom: 0.5rem;
}

.price{
  font-size: 2rem;
}

.rating span{
  color: gold;
  margin: 0.1rem;
}
